body {
  margin: 0;
  background-color: rgba(0, 0, 0, 1);
}

body::-webkit-scrollbar {
  display: none;
}

h3 {
  margin: 0px;
  color: rgba(250, 250, 250, 1);
  font-weight: 400;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  text-align: left;
  cursor: default;
  user-select: none;
}

input {
  box-sizing: border-box;
  padding: 16px;
  color: rgba(220, 220, 220, 1);
  font-size: 16px;
  font-weight: 900;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  outline: none;
  border-radius: 0;
  border: none;
  border-left: 2px solid rgba(200, 100, 40, 1);
  border-right: 2px solid rgba(200, 100, 40, 1);
  background-color: transparent;
}

textarea {
  box-sizing: border-box;
  resize: vertical;
  padding: 16px;
  color: rgba(220, 220, 220, 1);
  font-size: 16px;
  font-weight: 900;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  outline: none;
  border-radius: 0;
  border: none;
  border-left: 2px solid rgba(200, 100, 40, 1);
  border-right: 2px solid rgba(200, 100, 40, 1);
  background-color: transparent;
}

textarea::-webkit-scrollbar {
  display: none;
}

select {
  box-sizing: border-box;
  padding: 16px;
  color: rgba(220, 220, 220, 1);
  font-size: 16px;
  font-weight: 900;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  outline: none;
  border-radius: 0;
  border: none;
  background-color: rgba(0, 0, 0, 0.5);
  cursor: default;
  user-select: none;
}

option {
  color: rgba(220, 220, 220, 1);
  font-size: 16px;
  font-weight: 900;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

button {
  box-sizing: border-box;
  padding: 16px;
  color: rgba(220, 220, 220, 1);
  font-size: 16px;
  font-weight: 900;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  outline: none;
  border-radius: 0;
  border: none;
  border-left: 2px solid rgba(200, 100, 40, 1);
  border-right: 2px solid rgba(200, 100, 40, 1);
  background-color: rgba(200, 100, 40, 1);
  cursor: pointer;
  user-select: none;
  filter: brightness(100%);
  transition: 0.2s ease;
}

button:hover {
  filter: brightness(110%);
  transition: 0.2s ease;
}

button:active {
  background-color: rgba(200, 100, 40, 0.5);
  filter: brightness(80%);
  transform: scale(98%);
  transition: 0.05s ease;
}

.switcher {
  box-sizing: border-box;
  padding: 4px;
  height: 30px;
  width: 60px;
  color: rgba(220, 220, 220, 1);
  font-size: 16px;
  font-weight: 900;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  outline: none;
  border-radius: 999px;
  border: 2px solid rgba(250, 250, 250, 0.1);
  background-color: rgba(250, 250, 250, 0.1);
  cursor: pointer;
  user-select: none;
  transition: 0.2s ease;
}

.switcher.active {
  border: 2px solid rgba(0, 0, 0, 1);
  background-color: rgba(200, 100, 40, 1);
  transition: 0.3s ease;
}

.switcher-circle {
  position: relative;
  left: 0;
  height: calc(100%);
  width: calc(100% / 2 - 5.8px);
  border-radius: 999px;
  border: none;
  background-color: rgba(250, 250, 250, 1);
  transition: 0.3s ease;
}

.switcher-circle.active {
  left: calc(100% - (100% / 2 - 5.8px));
  background-color: rgba(250, 250, 250, 1);
  transition: 0.5s ease;
}

#root {
  display: flex;
  gap: 0;
  overflow-x: hidden;
  overflow-y: scroll;
  height: auto;
  background-color: rgba(0, 0, 0, 1);
}

.window {
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  height: 0;
  width: 100%;
  opacity: 0;
  z-index: 4;
  transition:
    height 0s 0.5s,
    opacity 0.5s 0s ease;
}

.window.shown {
  height: 100%;
  opacity: 1;
  transition:
    height 0s 0s,
    opacity 0.5s 0s ease;
}

.window-background {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  backdrop-filter: blur(0);
  background-color: rgba(0, 0, 0, 0.5);
}

.window-content {
  overflow-x: hidden;
  overflow-y: scroll;
  position: absolute;
  top: 50%;
  left: 50%;
  max-height: 100%;
  transform: translate(-50%, -50%);
}

.error-content {
  box-sizing: border-box;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  gap: 8px 0;
  padding: 10px;
  height: auto;
  width: 400px;
  backdrop-filter: blur(8px);
  border-radius: 0;
  border-left: 2px solid rgba(200, 100, 40, 1);
  border-right: 2px solid rgba(200, 100, 40, 1);
}

.error-content-icon {
  position: relative;
  left: 50%;
  height: 30px;
  width: 30px;
  transform: translateX(-50%);
  text-align: center;
  color: rgba(200, 100, 40, 1);
  font-size: 30px;
  cursor: default;
  user-select: none;
}

.error-content-title {
  position: relative;
  height: auto;
  width: 100%;
  text-align: center;
  color: rgba(250, 250, 250, 1);
  font-size: 20px;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-weight: 900;
  cursor: default;
  user-select: none;
}

.error-content-description {
  position: relative;
  height: auto;
  width: 100%;
  text-align: center;
  color: rgba(180, 180, 180, 1);
  font-size: 16px;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  cursor: default;
  user-select: none;
}

.error-content-submit {
  position: relative;
  height: auto;
  width: 100%;
  font-size: 16px;
  border: none;
  background-color: transparent;
}

.header {
