  border-radius: 6px;
  border: none;
  background-color: rgba(250, 250, 250, 0.05);
  cursor: pointer;
  user-select: none;
}

.header-authorize-button:active {
  transform: translateY(-50%);
  background-color: rgba(250, 250, 250, 0.05);
  border: none;
}

.authorize-content {
  box-sizing: border-box;
  padding: 10px;
  height: auto;
  min-width: 400px;
}

.authorize-content-login {
  position: relative;
  height: auto;
  width: 100%;
}

.authorize-content-password {
  position: relative;
  height: auto;
  width: 100%;
}

.authorize-content-button {
  position: relative;
  height: auto;
  width: 100%;
}

.header-user-name {
  position: absolute;
