.menu {
  box-sizing: border-box;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  gap: 21px 0;
  padding: 5px;
  position: relative;
  height: auto;
  width: 80px;
  background-color: transparent;
  transition: 0.5s ease;
  background-color: rgba(0, 0, 0, 1);
  z-index: 3;
}

.menu.hidden {
  width: 0;
}

.menu.big {
  width: 400px;
  transition:
  height 0.5s 0.5s ease,
  width 0.5s 0s ease,
  rotate 0.5s 0s ease;
}

.menu.big .menu-bars {
  rotate: -90deg;
  transition: 0.1s;
}

.menu.big .menu-element-name {
  height: auto;
  width: auto;
}

.menu-bars {
  position: relative;
  height: 70px;
  width: 70px;
  text-align: center;
  color: rgba(250, 250, 250, 1);
  font-size: 20px;
  line-height: 3.6;
  cursor: pointer;
  user-select: none;
  transition: 0.1s;
}

.menu-container {
  display: flex;
  flex-direction: column;
  gap: 8px 0;
  position: relative;
  height: auto;
  width: 100%;
}

.menu-element {
  box-sizing: border-box;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  padding-left: 8px;
  position: relative;
  height: 70px;
  width: 100%;
  text-align: left;
  text-decoration: none;
  color: rgba(250, 250, 250, 1);
  font-size: 20px;
  font-weight: 900;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  line-height: 3.6;
  background-color: transparent;
  cursor: pointer;
  user-select: none;
}

.menu-element.active {
  transform: scale(1.15);
  color: rgba(200, 100, 40, 1);
  transition: 0.2s ease;
}

.menu-element-name {
  overflow: hidden;
  position: relative;
  height: 0;
  width: 0;
  text-align: left;
  cursor: pointer;
  user-select: none;
}

.content {
  margin-top: 80px;
